@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body {
  margin: 0;
  padding: 0;
 background-color: #12172b;;
}

 

.NavbarItems {
    background: rgb(2,0,36);
    background-color:linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);;
    height: 100px;
    display: grid;
    grid-template-columns: repeat(2,auto);
    grid-gap: 60%;
     align-items: center; 
}
.example-class{
   cursor: pointer;
  max-width: 100%;
  width: 150px;
  height: auto;
   margin-top:-10px;
position: absolute;
 }
.navbar-logo {
    color: rgb(254, 254, 255);
     margin-top:2px;
    cursor: pointer;
    font-size: 30px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif  ;
    display: block;
    width: 100px;
    padding: 20px 110px;
}

.nav-button {
    width: 180px;
    height: 50px;
    border: 0;
    border-radius: 90px;
    font-size: 20px;
    background: #fff;;
    color: rgb(2,0,36); ;
    position: relative;
    transition: all .3s;
    cursor: pointer;
     font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif  ;
} 
  .nav-button:hover {
    background:  #00d4ff;
  }
  
  .nav-button:hover:after {
    -webkit-text-fill-color: rgb(20, 2, 2);
  }
  

@media screen and (max-width: 600px) {
    .NavbarItems {
        position: relative;
        grid-gap: 10%;
    }

    .navbar-logo{
        margin-top: 10px;
        font-size: large;
    }
}
* {
    padding: 0;
    margin: 0;
  }
  body {
    background-color: #12172b;
    font-family: "Poppins", sans-serif;
  }
  .card {
    margin: 20px 40px;
    color: white;
  }
  .card-img {
    display: flex;
    justify-content: space-around;
    margin: 20px 80px;
    flex-basis: 260px;
  }
  .card-img-top{
    height: 200px;
    width: 200px;
    border-radius: 100%;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    cursor: pointer;
    transition: 400ms;
  }
 .card-img-top:hover{
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
  }
.card-title{
    text-align: center;
    font-size: 25px;
    font-weight: 100;
    letter-spacing: 1px;
    color: #fff;
    cursor:pointer;
}
.email{
    font-size: 20px;
  margin-top: 10px;
  text-align: center;
  color: #ccc;

}
.card:hover{
  box-shadow: 0 0 15px 10px   #00d4ff;
    transition: all 0.2s ease-out;
  padding:30px;
}
  @media screen and (max-width: 1150px) {
    .body {
        flex-direction: column;
      }
    .card {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .card:hover{
      box-shadow: 0 0 15px 10px   #00d4ff;
        transition: all 0.2s ease-out;
      padding:30px;
    }
}
.footer{
    background-color:  #00d4ff;
    padding: 20px;
   -webkit-font-smoothing: antialiased;
   
 
 }
.footer .text-center{
    text-align: center;
    font-size: 20px;
 font-family: Georgia, serif;
}
@media screen and (max-width: 600px) {

	 .footer{
 		width: 100%;
		margin-bottom: 40px;
		text-align: center;
        margin-top: 1350px;
  	}

	.footer{
		margin-left: 0;
	}

}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family:sans-serif;
}

.App {
  background: rgb(2,0,36);
  width: 100%;
  height: 200vh;
 }

.allcards {
  display: grid;
  grid-template-columns: repeat(3,auto);
  grid-gap: 20px;
  justify-content: space-evenly; 
}

.loader {
  text-align: center;
  justify-content: center;
 align-items: center; 
 display:flex;
 padding-top: 220px;
}

@media screen and (max-width: 600px) {
  .allcards {
    grid-template-columns: repeat(1,auto);
    background-color: rgb(2,0,36)
  }
}

